.header {
  position: sticky;
  top: 0;
  background-color: black;
  padding: 30px;
  display: flex;
  align-items: center;
  z-index: 100;
}

.title {
  font-size: 28px;
  font-weight: bold;
  color: white;
  padding-right: 1rem;
}

.button {
  font-weight: bold;
  align-items: center;
  appearance: none;
  border-radius: 24px;
  border-style: none;
  box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px, rgba(0, 0, 0, .14) 0 6px 10px 0, rgba(0, 0, 0, .12) 0 1px 18px 0;
  box-sizing: border-box;
  color: white;
  cursor: pointer;
  display: inline-flex;
  fill: currentcolor;
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 16px;
  font-weight: 500;
  height: 30px;
  justify-content: center;
  letter-spacing: .25px;
  line-height: normal;
  max-width: 100%;
  overflow: visible;
  padding: 2px 13px;
  position: relative;
  text-align: center;
  text-transform: none;
  transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1), opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: auto;
  will-change: transform, opacity;
  z-index: 0;
}

.button:hover {
  font-weight: bold;
  color: var(--accentColor);
}

.button.active {
  font-weight: bold;
  background: #F6F9FE;
  color: var(--accentColor);
}

@media screen and (max-width: 600px) {
  .title {
    padding-right: 0.75rem; 

  }

 .button {
    font-size: 14px; 
  }

  
}